.SearchBar {
    //position: absolute;
    background-color: $navbarBgColor;
    box-shadow: 0 15px 20px rgba(0, 0, 0, 0.2);
    border-top: 0;
    border-radius: 0 0 5px 5px;
    padding: 10px;

    background: linear-gradient(90deg, #312c2c 13%, #312c2c 100%);

    width: 100%;
    left: 0;

    position: fixed;
    top: 51px;
    z-index: 400;

    form {
        margin: 0;
    }

    &.with-errors {
        padding-bottom: 30px;
    }

    .has-error .form-control {
        color: lighten($secondaryColor, 15%);
    }

    .help-block {
        position: absolute;
        color: lighten($secondaryColor, 15%);
    }

    @media (min-width: $screen-sm-min) {
        width: 500px;
        left: 50%;
        margin-left:  -250px;
    }

    @media (min-width: $screen-md-min) {
        width: 900px;
        left: 50%;
        margin-left:  -450px;
    }

    .form-group {
        margin-bottom: 0;

        label {
            color: $navbarLinkColor;
        }

        width: 100%;

        @media (max-width: $screen-sm-max) {
            margin-bottom: 10px;

            &.has-error {
                padding-bottom: 30px;
            }
        }


        &.withProfileImage {
            .btn-primary {
                background-color: #69595c;
                border-color: #775e63;
                color: #ccc;
                font-weight: 500;
                outline: 0;
                height: 36px;

                &:focus {
                    outline: 0;
                }

                &:hover {
                    outline: 0;
                    background-color: #9d515e;
                    border-color: #a98a90;
                    color: #fff;
                }

                &.active {
                    color: #fff;
                    outline: 0;
                    background-color: #c77c8a;
                    border-color: #a98a90;
                }
            }
        }

        @media (min-width: $screen-md-min) {
            padding-right: 20px;

            &.city {
                width: 25%;
            }

            &.radius,
            &.age,
            &.bodyType {
                width: 15%;
            }

            &.height {
                width: 20%;
            }

            &.withProfileImage {
                width: 25%;
            }

            &.submit {
                width: 70px;
                position: absolute;
                right: 10px;
                bottom: 9px;
                padding-right: 0px;

                button {
                    width: 70px;
                    height: 36px;
                    text-align: center;
                    padding: 0;
                }

                .material-icons {
                    position: relative;
                    font-size: 20px;
                    color: #fff;
                }
            }
        }


        &.submit {
            button {
                text-align: center;
                background-color: lighten($secondaryColor, 10%);

                &:hover {
                    background-color: lighten($secondaryColor, 5%);
                }
            }

            .material-icons {
                position: relative;
                font-size: 20px;
                color: #fff;
            }
        }
    }

    form {
        display: flex;

        flex-direction: column;

        @media (min-width: $screen-md-min) {
            flex-direction: row;
            align-items: center;
        }
    }
}


